/* Border*/
html {
  justify-content: space-around;
  flex-direction: row;
  display: flex; }

body {
  margin: 0;
  padding: 2%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.row {
  display: flex;
  flex-direction: row; }

.column {
  display: flex;
  flex-direction: column; }

.btn {
  outline: none;
  cursor: pointer;
  padding: 3px 15px;
  border: 0;
  background-color: #303A41;
  color: #FFFFFF;
  font-size: 13px; }

.btn:active {
  background-color: #761E2C; }

.active-button {
  background-color: #E04860; }

.active-link {
  color: #E04860; }

.link {
  outline: none;
  cursor: pointer; }

.link:active {
  color: #761E2C; }

.fractional-border {
  padding: 6px; }

.integer-border {
  padding: 5px 11px; }
